.possibility {
  display: flex;
  justify-content: space-between;
}
.possibility .image {
  display: flex;
  flex: 1;
  justify-content: flex-start;
  align-items: flex-start;
  margin-right: 2rem;
}
.possibility .image img {
  width: 100%;
  height: 100%;
}
.possibility .content {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-items: flex-start;
  flex-direction: column;
}
.possibility .content h4 {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;

  color: #71e5ff;
}
.possibility .content h4:last-child {
  color: var(--color-subtext);
}
.possibility .content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}
.possibility .content p {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  margin: 1rem 0;

  color: var(--color-text);
}
@media screen and (max-width: 950px) {
  .possibility {
    flex-direction: column;
  }
  .possibility .content {
    margin: 2rem 0 0 0;
  }
}
